<template>
  <div id="app">
    <div style="position:fixed;top:0">
      <select id="select">
        <option value="ours">ours</option>
        <option value="m">mobilenet</option>
        <option value="s">shufflenet</option>
        <option value="oursno">ours-noattention</option>
      </select>
      <button id="jia">加载模型</button>
      <button id="ce">图片平均时间</button>
    </div>

    <canvas id="canvas" width="256" height="256" style="width: 256px;height: 256px;"></canvas>
    <canvas id="canvas2" width="256" height="256" style="width: 256px;height: 256px;"></canvas>

    <img id="img1" width="256" height="256" style="width: 256px;height: 256px;" src="/1.jpg" />
    <img id="img2" width="256" height="256" style="width: 256px;height: 256px;" src="/2.jpg" />
    <img id="img3" width="256" height="256" style="width: 256px;height: 256px;" src="/3.jpg" />
    <img id="img4" width="256" height="256" style="width: 256px;height: 256px;" src="/4.jpg" />
    <img id="img5" width="256" height="256" style="width: 256px;height: 256px;" src="/5.jpg" />
    <img id="img6" width="256" height="256" style="width: 256px;height: 256px;" src="/6.jpg" />
    <img id="img7" width="256" height="256" style="width: 256px;height: 256px;" src="/7.jpg" />
    <img id="img8" width="256" height="256" style="width: 256px;height: 256px;" src="/8.jpg" />
    <img id="img9" width="256" height="256" style="width: 256px;height: 256px;" src="/9.jpg" />
    <img id="img10" width="256" height="256" style="width: 256px;height: 256px;" src="/10.jpg" />
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs'


export default {


  name: 'App',
  components: {

  },
  data() {
    return {
      // MODEL: null,
      // MODEL_URL: '',

    };
  },

  mounted() {


    //const MODEL_URL = 'http://localhost:8080/v7/model.json';

    var canvas = document.getElementById('canvas');
    var canvas2 = document.getElementById('canvas2');
    let ow = canvas.width;
    let oh = canvas.height;

    var img = new Image();

    img.src = '/6.jpg';
    img.onload = function () {
      var context = canvas.getContext('2d');
      context.drawImage(this, 0, 0, ow, ow);
    }

    var MODEL_URL = null;
    var MODEL = '';




    document.getElementById('jia').onclick = function () {
      MODEL_URL = `/${document.getElementById('select').value}/model.json`;
      new Promise((r) => {
        let a = tf.loadGraphModel(MODEL_URL);
        r(a)
      }).then((a) => {
        MODEL = a
        //console.log(MODEL);

        tf.tidy(() => {
          const t1 = new Date();
          //读取数据
          let x = tf.browser.fromPixels(canvas);
          //resize
          x = tf.image.resizeBilinear(x, [256, 256])
          //归一化
          x = tf.cast(x, 'float32').div(255).mul(2).sub(1).reshape([1, 256, 256, 3]);
          x = MODEL.predict(x);
          //resize
          x = tf.image.resizeBilinear(x, [oh, ow])
          //每个像素分类最大值的索引
          x = tf.argMax(x, -1);
          //变成oh*ow大小
          x = tf.reshape(x, [oh, ow]);
          //toPixels 前数据类型转化
          x = tf.cast(x, 'float32')
          tf.browser.toPixels(x, canvas2)
          const t4 = new Date();
          console.log('model predict take time: ', (t4 - t1), 'ms');
          alert('加载完毕：' + (t4 - t1) + 'ms')
          

        })
      })
    }
    document.getElementById('ce').onclick = function () {
      
      let alltime = 0;
      for (let i = 1; i < 11; i++) {
        tf.tidy(() => {
          const t1 = new Date();
          //读取数据
          let x = tf.browser.fromPixels(document.getElementById('img' + i));
          //resize
          x = tf.image.resizeBilinear(x, [256, 256])
          //归一化
          x = tf.cast(x, 'float32').div(255).mul(2).sub(1).reshape([1, 256, 256, 3]);
          x = MODEL.predict(x);
          //resize
          x = tf.image.resizeBilinear(x, [oh, ow])
          //每个像素分类最大值的索引
          x = tf.argMax(x, -1);
          //变成oh*ow大小
          x = tf.reshape(x, [oh, ow]);
          //toPixels 前数据类型转化
          x = tf.cast(x, 'float32')
          tf.browser.toPixels(x, canvas2)
          const t4 = new Date();
          let cost = t4 - t1;
          console.log(`第${i}个图片预测时间：`, cost, 'ms');
          alltime += cost
          if (i == 10) {
            let average = alltime / 10
            console.log('平均 ：', average, 'ms');
            alert('平均 ：' + average + 'ms');

          }


        })

      }


    }











  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
